				"html": "<h1>Java Inheritance Basics</h1>\n<p>So great you have successfully created a Car class. But, wait, aren't Tesla cars supposed to be electric variants? I want an Electric car class, but it also should have the properties of the original <code>Car</code> class.</p>\n<p>Solution : <strong>Inheritance</strong>. Java provides a neat way to \"inherit\" parent properties :</p>\n<pre><code>public class Car {\n\n    private String name;\n    private String manufacturerName;\n\n    public Car(String name, String man) {\n        this.name = name;\n        this.manufacturerName = man;\n    }\n    // Getter method\n    public String getName() {\n        return name;\n    }\n    // Getter method\n    public String getManufacturerName() {\n        return manufacturerName;\n    }\n}\n\npublic class ElectricCar extends Car {\n\n    public ElectricCar(String name, String man) {\n        super(name, man);\n    }\n\n    public void charge() {\n     System.out.println(\"Charging ...\");\n    }\n}\n\nElectricCar modelS = new ElectricCar(\"Model S\",\"Tesla\");\n// prints Tesla\nSystem.out.println(modelS.getManufacturerName());\n// prints Charging ...\nmodelS.charge();\n</code></pre>\n<p><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=2\" alt=\":rocket:\" title=\":rocket:\"> <a href=\"https://repl.it/CJZY/0\">Run Code</a></p>\n<p>See here that the class <code>ElecticCar</code> inherits or <code>extends</code> the public methods from <code>Car</code> class, as well as has its own methods and properties. Cool way to pass on information!</p>\n<p>Also notice the usage of <a href=\"https://docs.oracle.com/javase/tutorial/java/IandI/super.html\">super</a> keyword here. Since our <code>Car</code> class had a constructor, so we have to initialize that constructor from the child class as well. We do that using the <code>super</code> keyword. Read more about <a>Inheritance here</a>.</p>",
